<template>
	<view class="body">
		<view class="head">
			<view class="sousuo">
				<input type="text" value="" confirm-type="search" class="hei_24" @confirm="search" v-model="sou_txt" @click="panduan" @input="shuruchange" />
				<view class="sou_tishi qian_24" v-if="tishi">
					<image src="@/static/img/sousuo.png" mode="" />
					输入您要查找的律师姓名
				</view>
			</view>
		</view>
		<view class="tab_top hui_26">
			<pickerAddress2 @change="change" class="aaaaaa">
				{{ dizhi }}
				<image src="@/static/img/shaixuan.png" mode=""></image>
			</pickerAddress2>

			<picker :range="zhuanchang_arry" @change="zhuanchang_change">
				{{ zhuanchang }}
				<image src="@/static/img/shaixuan.png" mode=""></image>
			</picker>

			<picker :range="paixu_arry" @change="paixu_change">
				{{ paixu }}
				<image src="@/static/img/shaixuan.png" mode=""></image>
			</picker>
		</view>
		<view class="zi_body tab_zi_body">
			<view class="lvshi_list">
				<view class="lvshi_item" v-for="(item, index) in 10">
					<view class="zixun lv_20" @click="tochat">
						<image src="@/static/img/xiaoxi.png" mode=""></image>
						在线咨询
					</view>
					<view class="lvshi_left" @click="go_zhuye()"><image src="@/static/img/tx.png" mode=""></image></view>
					<view class="lvshi_right">
						<view class="hei_30_bold lvshi_name">
							摩格洛克律师
							<image src="@/static/img/renzheng.png" mode=""></image>
						</view>
						<view class="hui_24 lv_suo">
							<text class="tese bai_20">特色律所</text>
							北京盈科律师事务所
						</view>
						<view class="qian_22 lvsuo_dizhi">
							<image src="@/static/img/dizhi.png" mode=""></image>
							杭州市江干区新业路228号来福士广场12层
						</view>
						<view class="qian_22 lvshi_item_fuwu">
							<view class="fuwu">
								<image src="@/static/img/fuwu.png" mode=""></image>
								已服务
								<text class="lv_22">4532</text>
								人
							</view>
							<view class="">
								<image src="@/static/img/xingxing.png" mode=""></image>
								评价
								<text class="lv_22">34</text>
								次
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
import unpopup from '@/components/uni-popup/uni-popup-share.vue';
import pickerAddress2 from '@/components/wangding-pickerAddress/wangding-pickerAddress.vue';
export default {
	components: {
		unpopup,
		pickerAddress2
	},

	onShow(option) {
		var that=this
		uni.getStorage({
		    key: 'zhuanchang',
		    success: function (res) {
		        that.zhuanchang=res.data
		    }
		
		}) 
		
	},
onLoad(option) {

},
	data() {
		return {
			dizhi: '不限地区',
			zhuanchang: '不限专长',
			zhuanchang_arry: ['不限专长', '婚姻家庭', '交通事故', '形势辩护', '劳动工伤', '房产纠纷', '债权债务', '合同事务', '公司经营', '损害赔偿', '知识产权', '征地拆迁'],
			paixu: '综合排序',
			paixu_arry: ['综合排序', '最新排序', '热门排序'],
			sou_txt: '',
			tishi: true
		};
	},
	created() {},
	//下拉刷新
	onPullDownRefresh: function() {
		// this.getList();

		setTimeout(function() {
			uni.showToast({
				title: '没有更多内容了',
				duration: 2000,
				icon: 'none'
			});
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// stopPullDownRefresh:function(){

	// },
	methods: {
		//上拉加载
		onReachBottom() {
			uni.showToast({
				title: '没有更多内容了',
				duration: 2000,
				icon: 'none'
			});
		},
		navigateBack() {
			uni.navigateBack();
		},
        // 按键搜索
		search(e) {
			console.log(e.detail.value);
		},
		panduan() {
			this.tishi = false;
		},
		shuruchange() {
			if (this.sou_txt < 1) {
				this.tishi = true;
			}
		},
		// 地区选择
		change(data) {
			console.log(data);
			// this.dizhi = data.data.join('')
			this.sheng = data.data[0];
			this.shi = data.data[1];
			this.dizhi = this.sheng + '-' + this.shi;
		},
		// 专长选择
		zhuanchang_change(e) {
			this.zhuanchang = this.zhuanchang_arry[e.detail.value];
		},
		// 排序选择
		paixu_change(e) {
			this.paixu = this.paixu_arry[e.detail.value];
		},
		tochat(){
			uni.navigateTo({
				url:'zaixian_wen?user='+6666
			})
		},
		go_zhuye(){
			uni.navigateTo({
				url: 'ls_zhuye'
			});
		}
	}
};
</script>

<style>
page {
	background-color: #f7f7f7;
}
.head_center input {
	width: 100%;
	height: 55rpx;
	background-color: #f3f3f3;
	border-radius: 28rpx;
}
.uni-searchbar {
	width: 100%;
	padding: 0;
}
.uni-searchbar__box {
	border-style: none !important;
	height: 55rpx !important;
}
.tab_top {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 16rpx 71rpx;
	background-color: #ffffff;
	position: fixed;
	top: 96rpx;
	width: 100%;
	box-sizing: border-box;
	z-index: 9;
}
.tab_top image {
	width: 16rpx;
	height: 8rpx;
	vertical-align: middle;
	margin-left: 22rpx;
}
.head_right image {
	width: 40rpx;
	height: 41rpx;
}
.lvshi_left image {
	width: 130rpx;
	height: 131rpx;
	border-radius: 100%;
	margin-right: 20rpx;
}
.lvshi_list {
	margin-top: 30rpx;
	background-color: #ffffff;
}
.lvshi_item {
	display: flex;
	position: relative;
	padding: 26rpx 20rpx;
	border-bottom: 2rpx solid #e8e8e8;
}
.lvshi_name image {
	width: 22rpx;
	height: 20rpx;
	margin-left: 11rpx;
}
.zixun {
	position: absolute;
	width: 140rpx;
	height: 44rpx;
	background-color: #ffffff;
	border-radius: 22rpx;
	border: solid 1rpx #0eb77e;
	text-align: center;
	line-height: 44rpx;
	right: 20rpx;
}
.zixun image {
	width: 18rpx;
	height: 18rpx;
	margin-right: 5rpx;
}
.lvshi_item_fuwu {
	display: flex;
	margin-top: 10rpx;
}
.lvshi_item_fuwu image {
	width: 25rpx;
	height: 23rpx;
	margin-right: 10rpx;
}
.lvshi_item_fuwu text {
	margin: 0 4rpx;
}
.lv_suo {
	margin: 10rpx 0;
}
.tese {
	background-color: #ffad2b;
	border-radius: 3rpx;
	margin-right: 9rpx;
	padding: 0 6rpx;
}
.lvsuo_dizhi image {
	width: 19rpx;
	height: 20rpx;
	margin-right: 12rpx;
}
.fuwu {
	margin-right: 60rpx;
}
.sousuo {
	width: 100%;
	position: relative;
}
.sou_tishi image {
	width: 25rpx;
	height: 26rpx;
	margin-right: 17rpx;
}
.sou_tishi {
	position: absolute;
	transform: translate(-50%, -50%); /* 50%为自身尺寸的一半 */
	left: 50%;
	top: 50%;
	pointer-events: none;
	display: flex;
	align-items: center;
}
.sousuo input {
	width: 100%;
	height: 55rpx;
	background-color: #f3f3f3;
	border-radius: 28rpx;
	padding-left: 20rpx;
	box-sizing: border-box;
}
</style>
